.header-container {
    .header-content {
        display: grid;
        grid-template-columns: repeat(2, 50%);
        align-items: center;
        margin: 30px auto;
        max-width: 700px;
        
        .navigation ul {
            display: flex;
            align-items: center;
            justify-content: space-around;
            
            li {
                transition: .3s ease;
                
                &:hover {
                    // color: #ffdd4b;
                    color: $color-blue;
                    scale: 1.1;
                    transition: .3s ease;
                }
            }
            .nav-active {
                color: $color-blue;
            }
        }
        h1 {
            text-align: right;
            // color: #ffdd4b;
            font-size: 1.7rem;
            margin-right: 10px;
            -webkit-animation: random 15s infinite;
            animation: random 15s infinite;
            
            @keyframes random {
                15% { color: #e3ff87; } 
                30% { color: #ff7c96; } 
                45% { color: #6ffffa; }
                60% { color: #ef6b30; } 
                75% { color: #bf69b1; } 
            }
            i {
                font-size: 1.6rem;
            }
        }   
    }
}