* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1rem;
}
$color-white: #fff;
$color-black: #282828;
$color-blue: #6ffffa;
$color-black-alpha: #00000082;
body {
    background: $color-black;
    color: $color-white;

}
li {
    list-style: none;
}
a {
    color: $color-white;
    text-decoration: none;
}
::selection {
    color: $color-black;
    background: $color-white;
}