.search-container {
    margin: 50px auto;
    min-width: 200px;
    max-width: 300px;
    text-align: center;

    .search-content {
        input[type=text] {
            background: none;
            border: none;
            border-bottom: 3px solid rgba(128, 128, 128, 0.506);
            color: $color-blue;
            outline: none;
            transition: .2s ease;
            text-align: center;
            padding: 5px;
            font-size: 1.2rem;
            width: 200px;
            
            &:focus {
                border: none;
                border-bottom: 3px solid $color-blue;
                border-radius: 0px;
                color: white;
                width: 300px;
                transition: .2s ease;
            }
        }
    }
}