.movie-card-content {
    flex: 1 0 40%;
    min-width: 200px;
    max-width: 300px;
    height: 400px;
    border: 0px solid $color-blue;
    transition: .2s ease-in-out;
    position: relative;
    overflow: hidden;
    margin: 1px;

    &:hover {
        border: 10px solid $color-black;
        transition: .2s ease-in-out;
        -webkit-box-shadow: inset 0px -50px 50px 20px #000000; 
        box-shadow: inset 0px -50px 50px 20px #000000;
        
        .movie-card-infos {
            display: block;
            transition: .3s ease;
            // -webkit-box-shadow: 0px -50px 50px 20px #000000; 
            // box-shadow: inset 0px -50px 50px 20px #000000;
        }
    }

    // &::after {
    //     content: "";
    //     position: absolute;
    //     left: 0;
    //     bottom: 0px;
    //     width: 0;
    //     height: 0;
    //     border-top: 50px solid transparent;
    //     border-right: 200px solid $color-black-alpha;
    //     // border-bottom: 50px solid transparent;
    //     // border-top: 20px solid $color-blue;
    // }
    

    .movie-card-infos {
        display: none;
        position: absolute;
        bottom: 0;
        left: -1px;
        width: 101%;
        // background: $color-black-alpha;
        background: rgb(0,0,0);
        background: linear-gradient(0deg, rgba(0,0,0,0.795639422761292) 0%, rgba(0,0,0,0.7032024479713761) 80%, rgba(255,255,255,0) 100%);
        text-align: center;
        transition: .3s ease;
        // border-top: 2px solid $color-blue;
        
        h2 {
            color: $color-blue;
            margin: 20px;
        }
        p {
            i {
                color: yellow;
            }
        }
    }
}